header {
  /*display:none;*/
  width: 100%;
  display: block;
}

.header-v2 {

  @media(max-width: $menu-mobile-breakpoint){
    display: none; 
  }
  
  @media(min-width:  $menu-mobile-breakpoint){
    display: block; 
  }

  font-family: var(--global-font-abc-whyte);

  h1 {
    .logo {
      background: url("../img/v2/vera-logo.svg") top left no-repeat transparent;
      background-size: contain;
      color: transparent;
      display: block;
      background-position: center center;
      width: 184px;
      height: 63px;
    }
  }

  .rowTop {
    background-color: #0E002C;
    height: 119px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;

    &-container{
      max-width: 1280px;
      width: 100%;
      display: flex;
      flex-direction: row;
      margin: 0 auto;
    }

    #header-v2-top-nav {
      display: flex;
      flex-direction: row;
      width: 100%;
      z-index: 1000;

      .clicky-menu {
        justify-content: stretch;
        margin: 0;
        padding: 0;
        list-style: none;
        width: 100%;
        max-height: 42px;
        justify-content: flex-start;
        font-weight: 300;
        font-size: 14px;
        font-style: normal;
      }

      @media (min-width: 540px) {
        .clicky-menu {
          display: flex;
        }

      }


      /* General Link & Button Styles */
      .clicky-menu a,
      .clicky-menu button {
        color: #FFF;
        text-decoration: none;
        background: transparent;
        height: 42px;
        font-weight: 300;
        font-size: 14px;
        font-style: normal;
        justify-content: left;
        align-content: center;
        flex: 1 1 0px;

      /*  background-color: yellow;*/
        .textWrap{
          pointer-events: none;
          display: flex;
          flex-direction: row;
          width: 110px;
          position: relative;
         /* background-color: red;*/

          .textWrap-icon{
            position: relative;
            left: 10px;
            top: 0;
            right: 0;
            content: url("../img/v2/menu-close.svg");
          }
        }

        &:hover{
          background-color: #2E1D97;
          .textWrap{
            .textWrap-icon{
              content: url("../img/v2/menu-open.svg");
            }
          }
        }
      }

      .clicky-menu [aria-expanded="false"] {
        border-bottom: 1px solid transparent;        
        width: fit-content;
        justify-content: center;
        .textWrap{
          .textWrap-icon{
            content: url("../img/v2/menu-close.svg");
          }
        }
      }

      .clicky-menu [aria-expanded="true"] {
        background-color: #2E1D97;
        border-bottom: 1px solid #3E2BB1;
        width: fit-content;
        justify-content: center;
        .textWrap{
          .textWrap-icon{
            content: url("../img/v2/menu-open.svg");
          }
        }
      }


      /* Top Level Items */
      .clicky-menu>li {
        position: relative;
        /*flex: 1 1 auto;*/
      /*  flex: 1 1 0px;*/
        display: flex;
        justify-content: stretch;
        flex-wrap: wrap;
        max-width: 152px;
        flex: 1 1 0px;
      }

      .clicky-menu>li>a,
      .clicky-menu>li>button {
       /* flex: 1 0 auto;*/
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        border: 0;
        font-size: inherit;
        line-height: 1.5;
        cursor: pointer;
      }

      @media (min-width: 540px) {

        .clicky-menu>li>a,
        .clicky-menu>li>button {
          justify-content: center;
          align-content: center;
          padding: 0;   
        }
      }

      /* Submenu Styles */

      .clicky-menu ul {
        min-width: 100%;
        margin-top: 0;
        padding: 0;
        list-style: none;
        background-color: #2E1D97;
        display: flex;
        flex-direction: column;
        align-items: center;
      }

      /* Responsive Submenu Behavior */
      .clicky-menu ul[aria-hidden="false"] {
        position: static;
        width: 100%;
        flex: 0 0 auto;
      }

      @media (min-width: 540px) {
        .clicky-menu ul[aria-hidden="false"] {
          position: absolute;
          width: auto;
        }
      }

      /* Submenu Links */

      .clicky-menu ul li {
        display: flex;
        flex-direction: row;
        justify-content: center;
        width: 100%;
       /* background-color: red;*/

        &:hover {
          background-color: #3E2BB1;
          text-decoration: underline;
        }

        a {
          display: flex;
          justify-content: center;
          width: 100%;
          background-color: transparent;
          height: inherit;
         /* background-color: yellow;*/
          padding-top: 10px;
          padding-bottom: 10px;

          .subItemWrap{
            pointer-events: none;
            width: 110px;
           /* background-color: red;*/
          }

          &:hover {
            background-color: transparent;
          }
        }

        &:last-child{
          a{
            padding-bottom: 24px;
          }
        }

        &:first-child{
          a{
            padding-bottom: 10px;
          }
        }
      }

    }

    .menu-left {
      display: flex;
      flex-direction: row;
      height: 100%;
      align-items: center;
      width: 100%;
      max-width: 510px;
      justify-content: flex-start;

    }

    .menu-center{
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      width: 100%;
      max-width: 260px;
    }


    .menu-right {
      display: flex;
      flex-direction: row;
      width: 100%;
      max-width: 510px;
      justify-content: flex-end;

      .search{
        display: block;
        width: 100%;
        position: relative;
        right: 0;
      }
      .donate {
        justify-content: end;
        align-items: center;
        display: flex;
      }
    }

  }

  .rowBottom {
    background-color: #160643;
    height: 67px;
    border-bottom: 2px solid #3E2BB1;
    width: 100%;
    z-index: 500;
    transition: all 0.2s ease;

    &-container{
      transition: all 0.2s ease;
      max-width: 1280px;
      width: 100%;
      display: flex;
      flex-direction: row;
      margin: 0 auto;
    }
  


    &.show-logo {
      #header-v2-bottom-nav {
        .isologo {
          height: 67px;
          align-content: center;
          box-sizing: border-box;
          display: flex;
          a{
            margin: 15px;
            height: auto;
            display: flex;
          }
          
        }
      }
    }

    &.hide-logo {
      #header-v2-bottom-nav {
        .isologo {
          display: none;
        }
      }
    }

    #header-v2-bottom-nav {
      display: flex;
      justify-content: center;
      flex-direction: row;
      width: 100%;

      .isologo {
        display: none;
      }

      .clicky-menu {
        /*justify-content: stretch;*/
        margin: 0;
        padding: 0;
        list-style: none;
        width: 100%;
        flex-shrink: initial;
        justify-content: center;
      }

      @media (min-width: 540px) {
        .clicky-menu {
          display: flex;
        }

      }

      ul.row-bottom-menu{
        height: 67px;
        background-color: #160643;
        border-bottom: 2px solid #3e2bb1;
        z-index: 500;
        ul{
          border-top: 2px solid #3e2bb1;
          z-index: 500;
        }
      }


      /* General Link & Button Styles */
      .clicky-menu a,
      .clicky-menu button {
        color: #FFF;
        text-decoration: none;
        font-size: 16px;
        font-weight: bold;
        background: transparent;
        justify-content: left;
        align-content: center;
        transition: background-size .9s;
        &:hover{
          background-color: #2E1D97;
        }
      }

      .clicky-menu [aria-expanded="true"] {
        background-color: #2E1D97;
      }


      /* Top Level Items */
      .clicky-menu>li {
        position: relative;
        /*flex: 1 1 auto;*/
       /* flex: 1 1 0px;*/
        display: flex;
        justify-content: stretch;
        /*flex-wrap: wrap;*/
        flex-wrap: inherit;
        margin: 0 17px;
      
        &:first-child{
          justify-content: flex-start;
          margin: 0;
          margin-right: 17px;
        }

        &:last-child{
          justify-content: flex-end;
          margin-left: 17px;
          margin-right: 0;
        }
      }

      .clicky-menu>li>a,
      .clicky-menu>li>button {
        flex: 1 0 auto;
        display: flex;
        padding: 0;
        justify-content: flex-start;
        flex-wrap: wrap;
        border: 0;
        font-size: inherit;
        line-height: 1.5;
        cursor: pointer;
        padding-left: 35px;
        padding-right: 35px;
      }

      .clicky-menu>li
      {
        >a {
          justify-content: center;
          @media (min-width: 540px) {
            justify-content: center;
          }
        }
        >button {
          justify-content: left;
          @media (min-width: 540px) {
            justify-content: left;
          }
        }
      }
   
      /* Submenu Styles */
      .clicky-menu ul {
        min-width: 100%;
        width: 12em;
        margin-top: 0;
        padding: 0;
        list-style: none;
        background-color: #2E1D97;
      }

      @media (min-width: 540px) {
        .clicky-menu {}
      }

      /* Responsive Submenu Behavior */
      .clicky-menu ul[aria-hidden="false"] {
        position: static;
        width: 100%;
        flex: 0 0 auto;
      }

      @media (min-width: 540px) {
        .clicky-menu ul[aria-hidden="false"] {
          position: absolute;
          //width: auto;
          min-width: 413px;
          z-index: 500;
        }
      }

      /* Submenu Links */

      .clicky-menu ul li {
        &:first-child{
          padding-top: 34px;
        }

        &:last-child{
          padding-bottom: 40px;
        }
        
        padding: 17px 20px 17px 35px;

        .hover-arrow {
          margin-bottom: 5px;
        }

        &:hover {
          background-color: #3E2BB1;

          .hover-arrow {
            padding-right: 30px;
            width: fit-content;
            background-image: url("../img/v2/arrow.svg");
            background-repeat: no-repeat;
            background-size: inherit;
            background-position: right 0px center;
          }
        }

        a {
          display: block;
          background-color: transparent;

          span {
            font-size: 14px;
            font-weight: 400;
            word-wrap: break-word;

          }

          &:hover {
            background-color: transparent;
          }
        }
      }
    }
  }
}

.header-mobile {
  &-row {
    background: #160643;
    height: 60px;
    border-bottom: 2px solid #3E2BB1
  }
}